<template>
    <div>
      <!-- Font awseome stylesheet -->
      <link rel="stylesheet" href="http://maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css"/>
    
      <div id="bg-map" class="bg-map">
        <l-map
          ref="map"
          v-model="zoom"
          v-model:zoom="zoom"
          :center="center"
          :options="{zoomControl:false, attributionControl:false}"
          crs="CRS.EPSG3857"
          :minZoom="minzoom"
          @mousedown="mouseDownHandler"
        >
    
        <!-- Leaflet control tools here: -->
        <l-control-scale position="bottomright" :imperial="false" :metric="true"></l-control-scale>  
        <l-control-layers position="topright"></l-control-layers>
        <l-control-zoom position="topright" />
      
    
        <!-- Background maps: -->
        <l-tile-layer
          v-for="tileProvider in tileProviders"
          :key="tileProvider.name"
          :name="tileProvider.name"
          :visible="tileProvider.visible"
          :url="tileProvider.url"
          :attribution="tileProvider.attribution"
          layer-type="base"
          :zIndex="baseZIndex" />
    

        </l-map>
      </div>
    </div>
    </template>
    
    <script>
    // import libraries + styles
    import {
      LMap,
      LTileLayer,
      LControlLayers,
      LControlScale,
      LControlZoom
    } from "@vue-leaflet/vue-leaflet";
    import "leaflet/dist/leaflet.css";
    
    
    export default {
      name: 'Map_Component',
      components: {
        LMap,
        LTileLayer,
        LControlLayers,
        LControlScale,
        LControlZoom
      },
      data() {
        return {
          // center: [47.313220, -1.319482],
          // map settings
          zoom: 10,
          minzoom: 10,
          center: [48.7667, 9.1833],
    
          // background map
          tileProviders: [
            {
              name: 'OpenStreetMap',
              visible: true,
              attribution:
                '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
              url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
            },
            {
              name: 'Stadia.AlidadeSmooth',
              visible: false,
              url: 'https://tiles.stadiamaps.com/tiles/alidade_smooth/{z}/{x}/{y}{r}.png',
              attribution:
                '&copy; <a href="https://stadiamaps.com/">Stadia Maps</a>, &copy; <a href="https://openmaptiles.org/">OpenMapTiles</a> &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors',
            },
            {
              name: 'ESRI.WorldTopoMap',
              visible: false,
              url: 'https://server.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer/tile/{z}/{y}/{x}',
              attribution:
                'Tiles &copy; Esri &mdash; Esri, DeLorme, NAVTEQ, TomTom, Intermap, iPC, USGS, FAO, NPS, NRCAN, GeoBase, Kadaster NL, Ordnance Survey, Esri Japan, METI, Esri China (Hong Kong), and the GIS User Community',
            },
          ],
    
          // tiled wms layers
          wmsurl:'http://'+process.env.VUE_APP_EC2_IP+'/api/geoserver/gwc/service/wms',
          format_tile: "image/png",
          baseZIndex: 1,
          layer_s2: null,
    
          // border polygons (geojson)
          geojsonOptions: {
            style: {fillColor: '#8fbcf2',
                    color: '#325ea8',
                    radius: 4}
          },
          gemeindeOptions: {
            style: {color: '#8f8888',
                    weight: 1.5,
                    fillOpacity: 0,
                    zIndex: 500}
          },
          regionOptions: {
            style: {color: '#8f8888',
                    weight: 3,
                    fillOpacity:0,
                    zIndex: 400}
          },
          kreisOptions: {
            style: {color: '#8f8888',
                    weight: 3,
                    fillOpacity:0,
                    zIndex: 401}
          },
          maskeOptions: {
            style: {fillColor: '#9e9d9b',
                    fillOpacity: 0.5,
                    weight: 0}
          },
          geojson: null,
          show_focus_regions: false,
  
    
          // scrolly markers and polygons
          marker: [0,0],
          scrolly_markers: [[0,0]],
          scroll_active: false,
          geojson_scrolly: null,
          markerOptions: {opacity: 0, width:"1px", height:"1px"},
    
          // popups on layer click
          popupOptions: {
            maxWidth: 400
          },
    
          // slider
          sideBySide: null,   
        };
      },
    
      async created () {
      
      },
    
      async beforeMount() {
        // HERE is where to load Leaflet components!
        this.mapIsReady = true;
    
      },
        
      mounted() { 
  
      },
      beforeUnmount() {
      },
      methods: {
    
    
        gotoNthPolygon(e) {
          this.geojson_scrolly = e;
        },
    
        zoomToPolygon() {
          var bbox = this.$refs.geojsonScrolly.leafletObject.getBounds();
          var map = this.$refs.map.leafletObject;
          map.flyToBounds(bbox, {padding: [300,300]});
        },
    
       
    

    
        mouseDownHandler() {
          this.$refs.map.leafletObject.addEventListener('mouseup', this.mouseupHandler);
          this.$refs.map.leafletObject.addEventListener('drag', this.dragHandler);
        },
    
        mouseupHandler(e){
          this.addMarker(e);
          this.cleanUpEventListeners();
        },
    
        dragHandler(){
          this.cleanUpEventListeners();
        },
    
        cleanUpEventListeners(){
          this.$refs.map.leafletObject.removeEventListener('mouseup', this.mouseupHandler);
          this.$refs.map.leafletObject.removeEventListener('drag', this.dragHandler)
        },
      

      },
      
    };
    </script>
    
    <style scoped>
    .bg-map {
      position: fixed;
      height: 100vh; 
      width: 100vw;
      margin: 0;
      z-index: 0;
    };
    :root {
      --toggle-bg-on: red;
    };
    </style>